.foot {
  font-size: 0.9em;
  background-color: #2699FB;
  width: 100vw;
  color: white;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 0;
  margin-top: 2rem;
  left: 0;
  z-index: 3;
  padding: 1rem;
}

@media (max-width: 960px) {
  .foot {
    grid-template-columns: auto;
    align-content: center;
    align-items: center;
    margin-top: 0.3rem;
    padding: 0.3rem;
  }
}

.foot div {
  color: white;
}

.foot div img {
  padding: 0;
  margin: 0;
  width: 5rem;
}

@media (max-width: 960px) {
  .foot div img {
    width: 4rem;
  }
}

.foot div table tr td a {
  color: white;
}

@media (max-width: 960px) {
  .foot div table tr td {
    padding: 0.3rem;
  }
}

.foot div ul {
  list-style: none;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.foot div ul li {
  padding: 0;
  margin: 0;
}

.foot div ul li a {
  color: white;
  padding: 0;
  margin: 0;
}

.row, .col {
  padding: 0 !important;
  margin: 0 !important;
}

.row img, .col img {
  padding: 0 !important;
  margin: 0 !important;
}

.links h4, .dwn h4 {
  font-size: 1.1rem !important;
  color: white;
}

.links a, .dwn a {
  font-size: 0.9rem !important;
}

@media (max-width: 960px) {
  .links, .dwn {
    display: none;
  }
}
